import React from "react"

import { Line } from 'react-chartjs-2';

import {
  dataMapping,
  sliderMapping,
} from "../../data"

function getData () {
  const labels = sliderMapping.map(phase => phase.label)
  return {
    labels: labels,
    datasets: dataMapping.map((destination, index) => {
      return {
        label: destination.destination,
        data: destination.schedule,
        fill: true,
        backgroundColor: destination.backgroundColor,
        tension: 0,
      }
    })
  }
}

const options = {
  legend: {
    display: false
  },
  maintainAspectRatio: false,
  tooltips: {
    enabled: false
  },
  scales: {
    yAxes: [{
      scaleLabel: {
        display: true,
        labelString: 'Token distribution over time'
      },
      stacked: true
    }]
  },
  responsive: true,
}

export default function Lines () {
  return (
    <div className="lines__container">
      <Line
        data={getData()}
        options={options}
        height={350}
        width={650}
      />
    </div>
  )
}
