import React, { useState } from "react"

import Slider from "../Slider/Slider"
import Lines from "../Lines/Lines"
import Pie from "../Pie/Pie"
import ThemedBox from "../ThemedBox/ThemedBox"

import {
  SLIDER_DEFAULT_VALUE,
  dataMapping,
  sliderMapping,
} from "../../data"

import "./Main.css"

function getLabelsFromDatasets () {
  return dataMapping.map(o => o.destination)
}

function getDataForScheduleIndex (index) {
  return {
    labels: getLabelsFromDatasets(),
    datasets: [
      {
        label: sliderMapping[index].label,
        data: dataMapping.map(o => o.schedule[index]),
        backgroundColor: dataMapping.map(o => o.backgroundColor),
      }
    ]
  }
}

export default function Main () {
  const [ data, setData ] = useState(getDataForScheduleIndex(SLIDER_DEFAULT_VALUE))
  const [ sliderLabel, setSliderLabel ] = useState(sliderMapping[SLIDER_DEFAULT_VALUE].label)
  const [ supply, setSupply ] = useState(sliderMapping[SLIDER_DEFAULT_VALUE].text)

  const onSliderChange = (val) => {
    setSliderLabel(sliderMapping[val].label)
    setSupply(sliderMapping[val].text)
    setData(getDataForScheduleIndex(val))
  }

  return (
    <div className="container">
      <div className="main__title">Token Release Schedule</div>
      <div className="main__charts row">
        <div className="main__charts-pie col-lg-6 col-12" >
          <Pie
            data={data}
          />
        </div>
        <div className="main__charts-lines col-lg-6 col-12 d-md-block d-none" >
          <Lines />
        </div>
      </div>
      <div className="main__slider">
        <Slider
          onSliderChange={onSliderChange}
        />
      </div>
      <div className="main__supply label">
        Slide to adjust the Emmission Phase.
      </div>
      <div className="main__supply">
        <ThemedBox>
          <div className="main__supply-upper">{sliderLabel} - Circulating Supply</div>
          <div className="main__supply-under">{supply}</div>
        </ThemedBox>
      </div>
    </div>
  )
}
