import React, { useEffect, useState } from "react"
import { Switch, FormControlLabel } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { Chart, Pie } from 'react-chartjs-2';

import { addCommasToNumber } from "../../utils"
import "./Pie.css"

function getPercentageLabel (label, value, data) {
  let sum = 0;
  let dataArr = data.datasets[0].data;
  dataArr.map(data => {
      sum += data;
  });
  let percentage = (value*100 / sum).toFixed(1)+"%";
  return `${label} - ${percentage}`;
}

function debounce(fn, ms) {
  let timer
  return _ => {
    clearTimeout(timer)
    timer = setTimeout(_ => {
      timer = null
      fn.apply(this, arguments)
    }, ms)
  };
}

function getNumberLabel(label, value) {
  return `${label} - ${addCommasToNumber(value)}`;
}


const StyledSwitch = withStyles({
  switchBase: {
    color: "#5551FF",
    '&$checked': {
      color: "#5551FF",
    },
    '&$checked + $track': {
      backgroundColor: "#5551FF",
    },
  },
  checked: {},
  track: {},
})(Switch);

export default function PieComponent ({ data }) {
  const [ method, setMethod ] = useState("percentages")
  const [ label, setLabel ] = useState("Show numbers")
  const [ checked, setChecked ] = useState(true)
  const [ legend, setLegend ] = useState({
    display: window.innerWidth > 500 ? true : false,
    position: "chartArea",
    align: "center",
    title: true
  })

  useEffect(() => {

    const debouncedHandleResize = debounce(function handleResize() {
      updateChart(window.innerWidth)
    }, 250)

    window.addEventListener('resize', debouncedHandleResize)

    return _ => {
      window.removeEventListener('resize', debouncedHandleResize)
    }
  })

  class Options {
    constructor() {
      this.labelMethod = "percentages"
    }
    updateLabelMethod(method) {
      return this.labelMethod = method
    }
    getOptions() {
      return {
        maintainAspectRatio: true ,
        animation: {
          duration: 0
        },
        layout: {
          padding: {
            right: legend.display === true ? -200 : 0,
            top: legend.display === true ? 0 : 10
          }
        },
        showAllTooltips: true,
        tooltips: {
          callbacks: {
            label: (tooltipItem, data) => {
              let label = data.labels[tooltipItem.index]
              let value = data.datasets[tooltipItem.datasetIndex].data[tooltipItem.index]
              return (this.labelMethod === "percentages")
                ? getPercentageLabel(label, value, data)
                : getNumberLabel(label, value)
            } // End of label
          }, // End of callbacks
        }, // End of tooltips
      } // End of return
    } // End of getOptions
  }
  const OptionsInstance = new Options()

  const updateChart = (windowWidth) =>{
    if ( windowWidth < 500 ) {
      setLegend({
        display: false,
        position: "chartArea",
        align: "center",
        title: true
      })
     } else {
      setLegend({
        display: true,
        position: "chartArea",
        align: "center",
        title: true
      })
     };
    }


  const toggleChecked = (event) => {

    if (event.target.checked) {
      OptionsInstance.updateLabelMethod("percentages")
      setLabel("Show numbers")
      setChecked(true)
    } else {
      OptionsInstance.updateLabelMethod("numbers")
      setLabel("Show percentages")
      setChecked(false)
    }
  };

  return (
    <div className="">
      {/* <div className="pie__toggle">
        <FormControlLabel
          control={<StyledSwitch size="small" checked={checked} onChange={toggleChecked}/>}
          label={label}
        />
      </div> */}
      <Pie
        data={data}
        options={OptionsInstance.getOptions()}
        legend={legend}
        redraw={true}
      />
    </div>
  )
}
