import React, { useState } from "react"

import { Slider } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import {
  SLIDER_DEFAULT_VALUE,
  sliderMapping
} from "../../data"

import "./Slider.css"

const CustomSlider = withStyles({
  root: {
    color: 'gray',
    margin: '7px',
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: "#fff",
    border: "2px solid currentColor",
    marginTop: -8,
    marginLeft: -12,
    "&:focus, &:hover": {
      boxShadow: "0px 0px 0px 8px rgba(84, 199, 97, 0.16)"
    },
    "&$active": {
      boxShadow: "0px 0px 0px 12px rgba(84, 199, 97, 0.16)"
    }
  },
  active: {},
  track: {
    color: "#5551FF50",
    height: 4,
    borderRadius: 4
  },
  rail: {
    color: "#5551FF30",
    height: 8,
    borderRadius: 4
  },
})(Slider)

export default function SliderComponent ({ onSliderChange }) {
  const onSliderChangeProxy = (e, val) => {
    return onSliderChange(val)
  }
  return (
    <div className="slider__container">
      <div className="slider__slider">
        <CustomSlider
          defaultValue={SLIDER_DEFAULT_VALUE}
          onChange={onSliderChangeProxy}
          valueLabelDisplay={"off"}
          step={1}
          marks={sliderMapping}
          min={0}
          max={sliderMapping.length - 1}
        />
      </div>
    </div>
  )
}
